// Setting Vue Router

// Import
import Vue from "vue";
import VueRouter from "vue-router";
import routes from "@/router/routes";

const router = new VueRouter({
    routes: routes,
    mode: 'history',
    base: '/fake-news-detection-chrome-extension/',
    linkExactActiveClass: "nav-item active",
});

Vue.use(VueRouter);


export default router;
