<template>
    <section class="hero has-background-primary-light">
        <div class="hero-body">
            <div class="columns">
                <div class="column is-half-widescreen is-offset-one-quarter-widescreen">
                    <p class="subtitle">We hope you liked <b>Fake News Detector Chrome Extension</b>.</p>
                    <p class="pb-3">If not, please compile this form and help us to understand how to improve.</p>
                    <hr class="solid">
                    <div class="pt-3">
                        <p><b>How much did you like Fake News Detector Chrome Extension?</b></p>
                        <b-rate size="is-large" v-model="rate"></b-rate>
                        <p class="pt-1 pb-2"><b>Why are you uninstalling Fake News Detector Chrome Extension?</b></p>
                        <b-field>
                            <b-radio v-model="uninstallReason"
                                     native-value="DON'T_NEED">
                                I don't need it, I check the veracity of the news manually
                            </b-radio>
                        </b-field>
                        <b-field>
                            <b-radio v-model="uninstallReason"
                                     native-value="DOESN'T_WORK">
                                It does not work well
                            </b-radio>
                        </b-field>
                        <b-field>
                            <b-radio v-model="uninstallReason"
                                     native-value="SLOW_DOWN">
                                It slows down my web navigation
                            </b-radio>
                        </b-field>
                        <b-field>
                            <b-radio v-model="uninstallReason"
                                     native-value="OTHER_REASONS">
                                Other reasons (write them in the comment section below)
                            </b-radio>
                        </b-field>
                        <p class="pt-2 pb-2"><b>Would you like to add a comment to tell us what to improve?</b></p>
                        <b-field>
                            <b-input maxlength="1600" type="textarea" v-model="comment"></b-input>
                        </b-field>
                        <b-button :disabled="!isFormValid"
                            class="is-primary is-fullwidth" @click="submitFeedback">Submit feedback</b-button>
                    </div>
                </div>
            </div>
            <b-modal
                v-model='openEmailResultDialog'
                :width='400' scroll="keep" can-cancel="false"
            >
                <div class='card p-4'>
                    <div class="columns">
                        <div class="column">
                            <p class="subtitle py-2 mb-1"><b>{{ emailSendResultTitle }}</b></p>
                            <p class=''> {{ emailSendResultBody }}</p>
                        </div>
                        <div class="column is-2">
                            <b-icon v-if="emailSendResult !== 'loading'"
                                    :type='emailSendColor' size="is-large"
                                    pack="fas"
                                    :icon="emailSendIcon"> </b-icon>
                        </div>
                    </div>
                    <b-progress class="py-2"
                                v-if="emailSendResult === 'loading'"
                                :type="emailSendColor"
                                size="is-medium"
                                indeterminate
                    ></b-progress>

                </div>
            </b-modal>
        </div>
    </section>
</template>

<script>
import axios from 'axios';

export default {
    name: 'UninstallFeedback',
    props: {},
    data() {
        return {
            rate: undefined,
            uninstallReason: undefined,
            comment: "",
            openEmailResultDialog: false,
            emailSendResult: 'loading'
        }
    },
    computed: {
        isFormValid() {
            return this.rate !== undefined && this.uninstallReason !== undefined;
        },
        emailSendColor() {
            if (this.emailSendResult === 'loading') {
                return 'is-primary'
            } else if (this.emailSendResult === 'success') {
                return 'is-success'
            } else {
                return 'is-danger'
            }
        },
        emailSendIcon() {
            if (this.emailSendResult === 'loading') {
                return ''
            } else if (this.emailSendResult === 'success') {
                return 'check-circle'
            } else {
                return 'times-circle'
            }
        },
        emailSendResultTitle() {
            if (this.emailSendResult === 'loading') {
                return 'I\'m sending'
            } else if (this.emailSendResult === 'success') {
                return 'Thank you'
            } else {
                return 'Oh no!'
            }
        },
        emailSendResultBody() {
            if (this.emailSendResult === 'loading') {
                return 'Aligning the gears...'
            } else if (this.emailSendResult === 'success') {
                return 'Email sent correctly'
            } else {
                return 'An error has occurred. Try later, please'
            }
        }
    },
    async created() {
    },
    methods: {
        submitFeedback() {
            let bodyMessage = "RATE: " + this.rate + "; REASON: " + this.uninstallReason + "; COMMENT: " + this.comment;
            let messageData = {
                'from_address': "chrome_extension_feedback",
                'to_address': 'crispogioele@gmail.com',
                'subject': 'FEEDBACK - Fake News Detector Chrome Extension',
                'body': bodyMessage
            }
            let _this = this
            _this.openEmailResultDialog = true
            _this.emailSendResult = 'loading'
            axios({
                url: 'https://93u9s5.deta.dev/send',
                method: 'post',
                data: messageData
            })
                // eslint-disable-next-line no-unused-vars
                .then(success => {
                    _this.emailSendResult = 'success';
                    _this.rate = undefined;
                    _this.uninstallReason = undefined;
                    _this.comment = '';
                })
                .catch(error => {
                    console.error(error)
                    _this.emailSendResult = 'error';
                })
                .then(() => {
                    setTimeout(function() {
                        _this.openEmailResultDialog = false
                    }, 4000)
                })
        }
    }
}
</script>

<style lang="css" scoped>
/* Solid border */
hr.solid {
    border-top: 2px solid #bbb;
}
</style>
